.nav{
    height: 50px;
    display: flex;
    justify-content: center;
    gap: 50px;
    background-color: #2c3e50;
    align-items: center;
}

.nav a{
    border: 2px solid white;
    padding: 5px;
    text-decoration: none;
    color: white;
}

nav .active{
    border: 2px solid red;
}